.flex-wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: 5px 20px;
    box-sizing: border-box;
    .nav-left,
    .nav-right{
        width: 50%;
        ul{
            list-style: none;
            li{
                display: inline-block;
                padding: 0 10px;
            }
        }
    }
    .nav-left{
        text-align: left;
    }
    .nav-right{
        text-align: right;
    }

    .nav-center{
        width: 40px;
        position: absolute;
        top:0;
        left: 0;
    }
  }